"use client";

import React, { useState, useEffect } from 'react';
import NavBar from '../components/NavBar';
import MovieCard from '../components/MovieCard';
import { Grid, Container } from '@mui/material';

export default function HomePage() {
    const [movies, setMovies] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        // Simulated API call to fetch movies
        setTimeout(() => {
            setMovies([
                { id: 1, title: 'Inception', rating: 8.8, genre: 'Sci-Fi' },
                { id: 2, title: 'The Dark Knight', rating: 9.0, genre: 'Action' },
                { id: 3, title: 'Interstellar', rating: 8.6, genre: 'Adventure' },
            ]);
            setLoading(false);
        }, 1000);
    }, []);

    return (
        <>
            {/* Navigation Bar */}
            <NavBar />

            <Container sx={{ py: 4 }}>
                <h1 className="text-3xl font-bold mb-4 text-white">Top Movies</h1>
                {loading ? (
                    <p className="text-white">Loading...</p>
                ) : (
                    <Grid container spacing={4}>
                        {movies.map((movie) => (
                            <Grid item xs={12} sm={6} md={4} key={movie.id}>
                                <MovieCard
                                    title={movie.title}
                                    rating={movie.rating}
                                    genre={movie.genre}
                                    onDetailsClick={() => alert(`Details of ${movie.title}`)}
                                />
                            </Grid>
                        ))}
                    </Grid>
                )}
            </Container>
        </>
    );
}
